import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CategoryProductsService } from '../../services/category-products.service';

@Component({
  selector: 'app-category-view',
  templateUrl: './category-view.page.html',
  styleUrls: ['./category-view.page.scss'],
})
export class CategoryViewPage implements OnInit {

  categoryId;
  categoryName;
  products;

  constructor(private route: ActivatedRoute, private categoryService: CategoryProductsService) {
    this.route.queryParams.subscribe(params => {
      this.categoryId = params.categoryId;
      this.categoryName = params.categoryName
    });

  }

  ngOnInit() {
    this.getProducts();
  }

  getProducts() {
    this.categoryService.getProductsFromCategory(this.categoryId)
      .subscribe(data => {
        this.products = data;
      })
  }

}
